// إصلاح مشكلة toLowerCase
const safeToLowerCase = (value: any): string => {
  if (typeof value === 'string') return value.toLowerCase();
  if (value && typeof value.toString === 'function') return value.toString().toLowerCase();
  return 'unknown';
};

// ----------------------------------------
// TYPES & INTERFACES
// ----------------------------------------



type Libraries = ("places" | "geometry" | "visualization")[];
const libraries: Libraries = ["places", "geometry", "visualization"];

interface Location {
  id: string;
  name: string;
  lat: number;
  lng: number;
  population?: number;
  type: string;
  radius: number;
}

// ... (rest of the interfaces will be added in the next steps)

interface Keyword {
  id: string;
  text: string;
  searchVolume: number;
  competition: string;
  cpc: number;
  type: string;
  matchType: string;
}

interface TimeSlot {
  id: string;
  startTime: string;
  endTime: string;
  days: string[];
}

interface AudienceItem {
  id: string;
  label: string;
  percentage: number;
  selected: boolean;
}

interface AudienceData {
  ageGroups: AudienceItem[];
  genders: AudienceItem[];
  interests: AudienceItem[];
}

interface AdAsset {
  id: string;
  type: 'headline' | 'description' | 'image' | 'video' | 'phone';
  content: string;
  file?: File;
  preview?: string;
  performance?: {
    ctr: number;
    impressions: number;
    clicks: number;
  };
}

interface CampaignGoal {
  id: string;
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  gradient: string;
  requirements: string[];
  bestFor: string[];
}

interface CampaignData {
  websiteUrl: string;
  websiteAnalysis: any;
  keywords: Keyword[];
  selectedLanguages: string[];
  locations: Location[];
  campaignType: string;
  selectedGoal: string;
  budget: {
    type: 'daily' | 'total';
    amount: number;
    currency: string;
  };
  schedule: {
    type: string;
    customSlots: TimeSlot[];
  };
  adAssets: AdAsset[];
  audienceData: AudienceData;
}

