const NewCampaignMasterFinal: React.FC = () => {
  // ----------------------------------------
  // STATE MANAGEMENT
  // ----------------------------------------
  
  const [currentStep, setCurrentStep] = useState(1);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysisProgress, setAnalysisProgress] = useState(0);
  const [showAnalysisModal, setShowAnalysisModal] = useState(false);
  
  const [campaignData, setCampaignData] = useState<CampaignData>({
    websiteUrl: '',
    websiteAnalysis: null,
    keywords: [],
    selectedLanguages: ['ar'],
    locations: [],
    campaignType: '',
    selectedGoal: '',
    budget: {
      type: 'daily',
      amount: 100,
      currency: 'SAR'
    },
    schedule: {
      type: 'all_time',
      customSlots: []
    },
    adAssets: [],
    audienceData: {
      ageGroups: [
        { id: '18-24', label: '18-24 سنة', percentage: 25, selected: true },
        { id: '25-34', label: '25-34 سنة', percentage: 35, selected: true },
        { id: '35-44', label: '35-44 سنة', percentage: 25, selected: true },
        { id: '45-54', label: '45-54 سنة', percentage: 15, selected: false },
        { id: '55+', label: '55+ سنة', percentage: 10, selected: false }
      ],
      genders: [
        { id: 'male', label: 'ذكور', percentage: 60, selected: true },
        { id: 'female', label: 'إناث', percentage: 40, selected: true }
      ],
      interests: [
        { id: 'technology', label: 'التكنولوجيا', percentage: 30, selected: true },
        { id: 'business', label: 'الأعمال', percentage: 25, selected: true },
        { id: 'lifestyle', label: 'نمط الحياة', percentage: 20, selected: true },
        { id: 'sports', label: 'الرياضة', percentage: 15, selected: false },
        { id: 'travel', label: 'السفر', percentage: 10, selected: false }
      ]
    }
  });

  const [selectedGoal, setSelectedGoal] = useState('');
  const [showGoalModal, setShowGoalModal] = useState(false);
  const [selectedCampaignType, setSelectedCampaignType] = useState('');
  const [showRequirementsModal, setShowRequirementsModal] = useState(false);
  const [currentRequirements, setCurrentRequirements] = useState<string[]>([]);

  // Google Maps
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || '',
    libraries,
  });

  const [map, setMap] = useState<google.maps.Map | null>(null);
  const [searchBox, setSearchBox] = useState<google.maps.places.SearchBox | null>(null);
  const searchInputRef = useRef<HTMLInputElement>(null);

  // ----------------------------------------
  // UTILITY FUNCTIONS
  // ----------------------------------------

  const generateStars = () => {
    return Array.from({ length: 50 }, (_, i) => (
      <motion.div
        key={i}
        className="absolute w-1 h-1 bg-white rounded-full opacity-70"
        style={{
          left: `${Math.random() * 100}%`,
          top: `${Math.random() * 100}%`,
        }}
        animate={{
          opacity: [0.3, 1, 0.3],
          scale: [0.5, 1, 0.5],
        }}
        transition={{
          duration: 2 + Math.random() * 2,
          repeat: Infinity,
          delay: Math.random() * 2,
        }}
      />
    ));
  };

  const formatNumber = (num: number): string => {
    if (num >= 1000000) {
      return (num / 1000000).toFixed(1) + 'M';
    } else if (num >= 1000) {
      return (num / 1000).toFixed(1) + 'K';
    }
    return num.toString();
  };

  const formatCurrency = (amount: number, currency: string = 'SAR'): string => {
    const symbols: { [key: string]: string } = {
      'SAR': 'ر.س',
      'USD': '$',
      'EUR': '€',
      'GBP': '£'
    };
    return `${amount.toLocaleString()} ${symbols[currency] || currency}`;
  };

  // ----------------------------------------
  // EVENT HANDLERS
  // ----------------------------------------

  const handleWebsiteAnalysis = useCallback(async () => {
    if (!campaignData.websiteUrl) return;

    setIsAnalyzing(true);
    setAnalysisProgress(0);
    setShowAnalysisModal(true);

    // محاكاة تحليل الموقع مع شريط التقدم
    const steps = [
      { progress: 20, message: 'فحص بنية الموقع...' },
      { progress: 40, message: 'استخراج الكلمات المفتاحية...' },
      { progress: 60, message: 'تحليل المحتوى...' },
      { progress: 80, message: 'تحليل الجمهور المستهدف...' },
      { progress: 100, message: 'اكتمل التحليل!' }
    ];

    for (const step of steps) {
      await new Promise(resolve => setTimeout(resolve, 1000));
      setAnalysisProgress(step.progress);
    }

    // محاكاة نتائج التحليل
    const mockKeywords: Keyword[] = [
      { id: '1', text: 'خدمات رقمية', searchVolume: 12000, competition: 'متوسط', cpc: 2.5, type: 'primary', matchType: 'broad' },
      { id: '2', text: 'تطوير مواقع', searchVolume: 8500, competition: 'عالي', cpc: 4.2, type: 'primary', matchType: 'exact' },
      { id: '3', text: 'تسويق إلكتروني', searchVolume: 15000, competition: 'متوسط', cpc: 3.1, type: 'secondary', matchType: 'phrase' },
      { id: '4', text: 'تصميم تطبيقات', searchVolume: 6200, competition: 'منخفض', cpc: 1.8, type: 'secondary', matchType: 'broad' },
      { id: '5', text: 'استشارات تقنية', searchVolume: 4800, competition: 'منخفض', cpc: 2.9, type: 'long-tail', matchType: 'exact' }
    ];

    const mockAnalysis = {
      title: 'شركة التقنيات المتقدمة',
      description: 'نقدم حلول تقنية متطورة للشركات والأفراد',
      industry: 'التكنولوجيا',
      targetAudience: 'الشركات الصغيرة والمتوسطة',
      mainServices: ['تطوير المواقع', 'التطبيقات المحمولة', 'التسويق الرقمي'],
      competitorAnalysis: {
        strength: 'قوي',
        opportunities: ['التوسع في الأسواق الجديدة', 'تطوير خدمات جديدة']
      }
    };

    setCampaignData(prev => ({
      ...prev,
      keywords: mockKeywords,
      websiteAnalysis: mockAnalysis
    }));

    setTimeout(() => {
      setIsAnalyzing(false);
      setShowAnalysisModal(false);
    }, 1000);
  }, [campaignData.websiteUrl]);

  const handleLocationSelect = useCallback((place: google.maps.places.PlaceResult) => {
    if (place.geometry?.location) {
      const newLocation: Location = {
        id: Date.now().toString(),
        name: place.name || place.formatted_address || 'موقع جديد',
        lat: place.geometry.location.lat(),
        lng: place.geometry.location.lng(),
        population: Math.floor(Math.random() * 500000) + 50000,
        type: place.types?.[0] || 'locality',
        radius: 10
      };

      setCampaignData(prev => ({
        ...prev,
        locations: [...prev.locations, newLocation]
      }));

      if (map) {
        map.panTo(place.geometry.location);
        map.setZoom(12);
      }
    }
  }, [map]);

  const removeLocation = useCallback((locationId: string) => {
    setCampaignData(prev => ({
      ...prev,
      locations: prev.locations.filter(loc => loc.id !== locationId)
    }));
  }, []);

  const updateLocationRadius = useCallback((locationId: string, radius: number) => {
    setCampaignData(prev => ({
      ...prev,
      locations: prev.locations.map(loc =>
        loc.id === locationId ? { ...loc, radius } : loc
      )
    }));
  }, []);

  const addTimeSlot = useCallback(() => {
    const newSlot: TimeSlot = {
      id: Date.now().toString(),
      startTime: '09:00',
      endTime: '17:00',
      days: ['monday', 'tuesday', 'wednesday', 'thursday', 'sunday']
    };

    setCampaignData(prev => ({
      ...prev,
      schedule: {
        ...prev.schedule,
        customSlots: [...prev.schedule.customSlots, newSlot]
      }
    }));
  }, []);

  const removeTimeSlot = useCallback((slotId: string) => {
    setCampaignData(prev => ({
      ...prev,
      schedule: {
        ...prev.schedule,
        customSlots: prev.schedule.customSlots.filter(slot => slot.id !== slotId)
      }
    }));
  }, []);

  const updateTimeSlot = useCallback((slotId: string, updates: Partial<TimeSlot>) => {
    setCampaignData(prev => ({
      ...prev,
      schedule: {
        ...prev.schedule,
        customSlots: prev.schedule.customSlots.map(slot =>
          slot.id === slotId ? { ...slot, ...updates } : slot
        )
      }
    }));
  }, []);

  const addAdAsset = useCallback((type: AdAsset['type']) => {
    const newAsset: AdAsset = {
      id: Date.now().toString(),
      type,
      content: '',
      performance: {
        ctr: Math.random() * 5 + 1,
        impressions: Math.floor(Math.random() * 10000) + 1000,
        clicks: Math.floor(Math.random() * 500) + 50
      }
    };

    setCampaignData(prev => ({
      ...prev,
      adAssets: [...prev.adAssets, newAsset]
    }));
  }, []);

  const updateAdAsset = useCallback((assetId: string, updates: Partial<AdAsset>) => {
    setCampaignData(prev => ({
      ...prev,
      adAssets: prev.adAssets.map(asset =>
        asset.id === assetId ? { ...asset, ...updates } : asset
      )
    }));
  }, []);

  const removeAdAsset = useCallback((assetId: string) => {
    setCampaignData(prev => ({
      ...prev,
      adAssets: prev.adAssets.filter(asset => asset.id !== assetId)
    }));
  }, []);

  const handleGoalSelect = useCallback((goalId: string) => {
    setSelectedGoal(goalId);
    setCampaignData(prev => ({ ...prev, selectedGoal: goalId }));
    
    const goal = CAMPAIGN_GOALS.find(g => g.id === goalId);
    if (goal) {
      setCurrentRequirements(goal.requirements);
      setShowRequirementsModal(true);
    }
    setShowGoalModal(false);
  }, []);

  const handleCampaignTypeSelect = useCallback((typeId: string) => {
    setSelectedCampaignType(typeId);
    setCampaignData(prev => ({ ...prev, campaignType: typeId }));
  }, []);

  const calculatePerformancePredictions = useMemo(() => {
    const baseImpressions = campaignData.budget.amount * 100;
    const baseCtr = 2.5;
    const baseCpc = 2.0;

    return {
      conservative: {
        impressions: Math.floor(baseImpressions * 0.8),
        clicks: Math.floor(baseImpressions * 0.8 * (baseCtr * 0.8) / 100),
        conversions: Math.floor(baseImpressions * 0.8 * (baseCtr * 0.8) / 100 * 0.02),
        cost: campaignData.budget.amount * 0.9
      },
      realistic: {
        impressions: baseImpressions,
        clicks: Math.floor(baseImpressions * baseCtr / 100),
        conversions: Math.floor(baseImpressions * baseCtr / 100 * 0.03),
        cost: campaignData.budget.amount
      },
      optimistic: {
        impressions: Math.floor(baseImpressions * 1.3),
        clicks: Math.floor(baseImpressions * 1.3 * (baseCtr * 1.2) / 100),
        conversions: Math.floor(baseImpressions * 1.3 * (baseCtr * 1.2) / 100 * 0.05),
        cost: campaignData.budget.amount * 1.1
      }
    };
  }, [campaignData.budget.amount]);

  const nextStep = () => {
    if (currentStep < 6) {
      setCurrentStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  // ----------------------------------------
  // RENDER FUNCTIONS
  // ----------------------------------------

  const renderStepIndicator = () => (
    <div className="flex items-center justify-center mb-8">
      <div className="flex items-center space-x-4 rtl:space-x-reverse">
        {[1, 2, 3, 4, 5, 6].map((step) => (
          <React.Fragment key={step}>
            <motion.div
              className={`w-12 h-12 rounded-full flex items-center justify-center text-sm font-bold transition-all duration-300 ${
                step === currentStep
                  ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white shadow-lg scale-110'
                  : step < currentStep
                  ? 'bg-gradient-to-r from-green-500 to-blue-500 text-white'
                  : 'bg-gray-700 text-gray-400'
              }`}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
            >
              {step < currentStep ? (
                <CheckCircle className="w-6 h-6" />
              ) : (
                step
              )}
            </motion.div>
            {step < 6 && (
              <div
                className={`w-8 h-1 transition-all duration-300 ${
                  step < currentStep ? 'bg-gradient-to-r from-green-500 to-blue-500' : 'bg-gray-700'
                }`}
              />
            )}
          </React.Fragment>
        ))}
      </div>
    </div>
  );



  // ----------------------------------------
  // STEP 1: تحليل الموقع الإلكتروني
  // ----------------------------------------
  
  const renderStep1 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-blue-900/50 to-purple-900/50 p-8 backdrop-blur-sm border border-blue-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-600/10 to-purple-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-blue-500 to-cyan-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <Brain className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-400 to-cyan-400 bg-clip-text text-transparent">
            تحليل الموقع الإلكتروني بالذكاء الاصطناعي
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            سيقوم نظام الذكاء الاصطناعي المتطور بتحليل موقعك الإلكتروني واستخراج أفضل الكلمات المفتاحية والمعلومات التسويقية لإنشاء حملة إعلانية مثالية
          </p>
        </motion.div>
      </div>

      {/* URL Input */}
      <div className="bg-gradient-to-br from-gray-900/80 to-blue-900/40 rounded-2xl p-8 backdrop-blur-sm border border-blue-500/20">
        <div className="flex items-center mb-6">
          <Globe className="w-6 h-6 text-blue-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">رابط الموقع الإلكتروني</h3>
        </div>
        
        <div className="space-y-4">
          <div className="relative">
            <input
              type="url"
              value={campaignData.websiteUrl}
              onChange={(e) => setCampaignData(prev => ({ ...prev, websiteUrl: e.target.value }))}
              placeholder="https://example.com"
              className="w-full px-6 py-4 bg-gray-800/50 border border-gray-600 rounded-xl text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 text-lg"
              dir="ltr"
            />
            <motion.div
              className="absolute left-4 top-1/2 transform -translate-y-1/2"
              whileHover={{ scale: 1.1 }}
            >
              <Globe className="w-5 h-5 text-gray-400" />
            </motion.div>
          </div>
          
          <motion.button
            onClick={handleWebsiteAnalysis}
            disabled={!campaignData.websiteUrl || isAnalyzing}
            className="w-full py-4 px-8 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl font-bold text-lg disabled:opacity-50 disabled:cursor-not-allowed hover:from-blue-700 hover:to-purple-700 transition-all duration-300 shadow-lg hover:shadow-xl"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            {isAnalyzing ? (
              <div className="flex items-center justify-center">
                <motion.div
                  className="w-6 h-6 border-2 border-white border-t-transparent rounded-full ml-3"
                  animate={{ rotate: 360 }}
                  transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                />
                جاري التحليل...
              </div>
            ) : (
              <div className="flex items-center justify-center">
                <Zap className="w-6 h-6 ml-3" />
                تحليل الموقع بالذكاء الاصطناعي
              </div>
            )}
          </motion.button>
        </div>
      </div>

      {/* Analysis Results */}
      {campaignData.websiteAnalysis && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="bg-gradient-to-br from-green-900/40 to-blue-900/40 rounded-2xl p-8 backdrop-blur-sm border border-green-500/20"
        >
          <div className="flex items-center mb-6">
            <CheckCircle className="w-6 h-6 text-green-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">نتائج التحليل</h3>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <div className="bg-gray-800/50 rounded-xl p-6">
                <h4 className="text-lg font-bold text-white mb-2">معلومات الموقع</h4>
                <div className="space-y-2 text-gray-300">
                  <p><span className="text-blue-400">العنوان:</span> {campaignData.websiteAnalysis.title}</p>
                  <p><span className="text-blue-400">الوصف:</span> {campaignData.websiteAnalysis.description}</p>
                  <p><span className="text-blue-400">المجال:</span> {campaignData.websiteAnalysis.industry}</p>
                </div>
              </div>
              
              <div className="bg-gray-800/50 rounded-xl p-6">
                <h4 className="text-lg font-bold text-white mb-2">الخدمات الرئيسية</h4>
                <div className="flex flex-wrap gap-2">
                  {campaignData.websiteAnalysis.mainServices.map((service: string, index: number) => (
                    <span
                      key={index}
                      className="px-3 py-1 bg-blue-600/20 text-blue-300 rounded-full text-sm border border-blue-500/30"
                    >
                      {service}
                    </span>
                  ))}
                </div>
              </div>
            </div>
            
            <div className="space-y-4">
              <div className="bg-gray-800/50 rounded-xl p-6">
                <h4 className="text-lg font-bold text-white mb-2">تحليل المنافسة</h4>
                <div className="space-y-2 text-gray-300">
                  <p><span className="text-green-400">القوة:</span> {campaignData.websiteAnalysis.competitorAnalysis.strength}</p>
                  <div>
                    <span className="text-green-400">الفرص:</span>
                    <ul className="list-disc list-inside mt-1 space-y-1">
                      {campaignData.websiteAnalysis.competitorAnalysis.opportunities.map((opp: string, index: number) => (
                        <li key={index} className="text-sm">{opp}</li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
              
              <div className="bg-gray-800/50 rounded-xl p-6">
                <h4 className="text-lg font-bold text-white mb-2">الجمهور المستهدف</h4>
                <p className="text-gray-300">{campaignData.websiteAnalysis.targetAudience}</p>
              </div>
            </div>
          </div>
        </motion.div>
      )}
    </motion.div>
  );

  // ----------------------------------------
  // STEP 2: الكلمات المفتاحية واللغة الذكية
  // ----------------------------------------
  
  const renderStep2 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-green-900/50 to-blue-900/50 p-8 backdrop-blur-sm border border-green-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-green-600/10 to-blue-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-green-500 to-blue-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <Key className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-green-400 to-blue-400 bg-clip-text text-transparent">
            الكلمات المفتاحية واللغة الذكية
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            الكلمات المفتاحية المستخرجة من موقعك الإلكتروني بواسطة الذكاء الاصطناعي مع إعدادات اللغة المتقدمة
          </p>
        </motion.div>
      </div>

      {/* Keywords from Website Analysis */}
      {campaignData.keywords.length > 0 && (
        <div className="bg-gradient-to-br from-gray-900/80 to-green-900/40 rounded-2xl p-8 backdrop-blur-sm border border-green-500/20">
          <div className="flex items-center mb-6">
            <Sparkles className="w-6 h-6 text-green-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">الكلمات المفتاحية المستخرجة من الموقع</h3>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {campaignData.keywords.map((keyword) => (
              <motion.div
                key={keyword.id}
                className="bg-gray-800/50 rounded-xl p-6 border border-gray-600/50 hover:border-green-500/50 transition-all duration-300"
                whileHover={{ scale: 1.02, y: -2 }}
              >
                <div className="flex items-center justify-between mb-3">
                  <h4 className="text-lg font-bold text-white">{keyword.text}</h4>
                  <span className={`px-2 py-1 rounded-full text-xs font-bold ${
                    keyword.type === 'primary' ? 'bg-green-600/20 text-green-300' :
                    keyword.type === 'secondary' ? 'bg-blue-600/20 text-blue-300' :
                    'bg-purple-600/20 text-purple-300'
                  }`}>
                    {keyword.type === 'primary' ? 'أساسي' : keyword.type === 'secondary' ? 'ثانوي' : 'طويل'}
                  </span>
                </div>
                
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-400">حجم البحث:</span>
                    <span className="text-green-400 font-bold">{formatNumber(keyword.searchVolume)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-400">المنافسة:</span>
                    <span className={`font-bold ${
                      keyword.competition === 'منخفض' ? 'text-green-400' :
                      keyword.competition === 'متوسط' ? 'text-yellow-400' : 'text-red-400'
                    }`}>
                      {keyword.competition}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-400">تكلفة النقرة:</span>
                    <span className="text-blue-400 font-bold">{keyword.cpc} ر.س</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-400">نوع المطابقة:</span>
                    <span className="text-purple-400 font-bold">
                      {keyword.matchType === 'exact' ? 'مطابقة تامة' : 
                       keyword.matchType === 'phrase' ? 'مطابقة عبارة' : 'مطابقة واسعة'}
                    </span>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      )}

      {/* Language Selection */}
      <div className="bg-gradient-to-br from-gray-900/80 to-blue-900/40 rounded-2xl p-8 backdrop-blur-sm border border-blue-500/20">
        <div className="flex items-center mb-6">
          <Languages className="w-6 h-6 text-blue-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">اختيار اللغات المستهدفة</h3>
        </div>
        
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {supportedLanguages.map((language) => (
            <motion.div
              key={language.id}
              className={`p-4 rounded-xl border-2 cursor-pointer transition-all duration-300 ${
                campaignData.selectedLanguages.includes(language.id)
                  ? 'border-blue-500 bg-blue-600/20'
                  : 'border-gray-600 bg-gray-800/50 hover:border-blue-400'
              }`}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={() => {
                setCampaignData(prev => ({
                  ...prev,
                  selectedLanguages: prev.selectedLanguages.includes(language.id)
                    ? prev.selectedLanguages.filter(id => id !== language.id)
                    : [...prev.selectedLanguages, language.id]
                }));
              }}
            >
              <div className="flex items-center space-x-3 rtl:space-x-reverse">
                <span className="text-2xl">{language.flag}</span>
                <div>
                  <h4 className="text-white font-bold">{language.name}</h4>
                </div>
                {campaignData.selectedLanguages.includes(language.id) && (
                  <CheckCircle className="w-5 h-5 text-blue-400 mr-auto" />
                )}
              </div>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Keywords Performance Prediction */}
      {campaignData.keywords.length > 0 && (
        <div className="bg-gradient-to-br from-gray-900/80 to-purple-900/40 rounded-2xl p-8 backdrop-blur-sm border border-purple-500/20">
          <div className="flex items-center mb-6">
            <BarChart3 className="w-6 h-6 text-purple-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">توقعات أداء الكلمات المفتاحية</h3>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-green-400 mb-2">
                {formatNumber(campaignData.keywords.reduce((sum, k) => sum + k.searchVolume, 0))}
              </div>
              <div className="text-gray-300">إجمالي حجم البحث الشهري</div>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-blue-400 mb-2">
                {(campaignData.keywords.reduce((sum, k) => sum + k.cpc, 0) / campaignData.keywords.length).toFixed(2)} ر.س
              </div>
              <div className="text-gray-300">متوسط تكلفة النقرة</div>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-purple-400 mb-2">
                {campaignData.keywords.length}
              </div>
              <div className="text-gray-300">عدد الكلمات المفتاحية</div>
            </div>
          </div>
        </div>
      )}
    </motion.div>
  );

  // ----------------------------------------
  // STEP 3: الموقع الجغرافي المتقدم (50% - 50%)
  // ----------------------------------------
  
  const renderStep3 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-orange-900/50 to-red-900/50 p-8 backdrop-blur-sm border border-orange-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-orange-600/10 to-red-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-orange-500 to-red-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <MapPin className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-orange-400 to-red-400 bg-clip-text text-transparent">
            الموقع الجغرافي المتقدم
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            حدد المناطق الجغرافية المستهدفة بدقة عالية مع خرائط تفاعلية متطورة ونطاقات قابلة للتخصيص
          </p>
        </motion.div>
      </div>

      {/* Map and Locations (50% - 50%) */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Map Section (50%) */}
        <div className="bg-gradient-to-br from-gray-900/80 to-orange-900/40 rounded-2xl p-6 backdrop-blur-sm border border-orange-500/20">
          <div className="flex items-center mb-6">
            <Map className="w-6 h-6 text-orange-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">الخريطة التفاعلية</h3>
          </div>
          
          {/* Search Box */}
          <div className="mb-4">
            {isLoaded && (
              <StandaloneSearchBox
                onLoad={(ref) => setSearchBox(ref)}
                onPlacesChanged={() => {
                  if (searchBox) {
                    const places = searchBox.getPlaces();
                    if (places && places.length > 0) {
                      handleLocationSelect(places[0]);
                    }
                  }
                }}
              >
                <input
                  ref={searchInputRef}
                  type="text"
                  placeholder="ابحث عن موقع..."
                  className="w-full px-4 py-3 bg-gray-800/50 border border-gray-600 rounded-xl text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all duration-300"
                />
              </StandaloneSearchBox>
            )}
          </div>
          
          {/* Google Map */}
          <div className="rounded-xl overflow-hidden border border-gray-600">
            {isLoaded ? (
              <GoogleMap
                mapContainerStyle={mapContainerStyle}
                center={campaignData.locations.length > 0 ? 
                  { lat: campaignData.locations[0].lat, lng: campaignData.locations[0].lng } : 
                  defaultCenter
                }
                zoom={10}
                options={mapOptions}
                onLoad={(map) => setMap(map)}
              >
                {campaignData.locations.map((location) => (
                  <React.Fragment key={location.id}>
                    <Marker
                      position={{ lat: location.lat, lng: location.lng }}
                      title={location.name}
                    />
                    <Circle
                      center={{ lat: location.lat, lng: location.lng }}
                      radius={location.radius * 1000}
                      options={{
                        fillColor: '#f97316',
                        fillOpacity: 0.2,
                        strokeColor: '#f97316',
                        strokeOpacity: 0.8,
                        strokeWeight: 2,
                      }}
                    />
                  </React.Fragment>
                ))}
              </GoogleMap>
            ) : (
              <div className="h-[500px] bg-gray-800 rounded-xl flex items-center justify-center">
                <div className="text-center">
                  <motion.div
                    className="w-12 h-12 border-4 border-orange-500 border-t-transparent rounded-full mx-auto mb-4"
                    animate={{ rotate: 360 }}
                    transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                  />
                  <p className="text-gray-400">جاري تحميل الخريطة...</p>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Selected Locations (50%) */}
        <div className="bg-gradient-to-br from-gray-900/80 to-red-900/40 rounded-2xl p-6 backdrop-blur-sm border border-red-500/20">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center">
              <Target className="w-6 h-6 text-red-400 ml-3" />
              <h3 className="text-2xl font-bold text-white">المواقع المختارة</h3>
            </div>
            <span className="bg-red-600/20 text-red-300 px-3 py-1 rounded-full text-sm font-bold">
              {campaignData.locations.length} موقع
            </span>
          </div>
          
          <div className="space-y-4 max-h-[500px] overflow-y-auto">
            {campaignData.locations.length === 0 ? (
              <div className="text-center py-12">
                <MapPin className="w-16 h-16 text-gray-600 mx-auto mb-4" />
                <p className="text-gray-400 text-lg">لم يتم اختيار أي مواقع بعد</p>
                <p className="text-gray-500 text-sm mt-2">استخدم البحث أعلاه لإضافة مواقع</p>
              </div>
            ) : (
              campaignData.locations.map((location) => (
                <motion.div
                  key={location.id}
                  className="bg-gray-800/50 rounded-xl p-4 border border-gray-600/50"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                >
                  <div className="flex items-start justify-between mb-3">
                    <div className="flex-1">
                      <h4 className="text-lg font-bold text-white mb-1">{location.name}</h4>
                      <p className="text-gray-400 text-sm">
                        {location.lat.toFixed(4)}, {location.lng.toFixed(4)}
                      </p>
                      {location.population && (
                        <p className="text-blue-400 text-sm">
                          عدد السكان: {formatNumber(location.population)}
                        </p>
                      )}
                    </div>
                    <motion.button
                      onClick={() => removeLocation(location.id)}
                      className="p-2 text-red-400 hover:text-red-300 hover:bg-red-600/20 rounded-lg transition-all duration-300"
                      whileHover={{ scale: 1.1 }}
                      whileTap={{ scale: 0.9 }}
                    >
                      <X className="w-5 h-5" />
                    </motion.button>
                  </div>
                  
                  <div className="space-y-3">
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">
                        نطاق الاستهداف: {location.radius} كم
                      </label>
                      <input
                        type="range"
                        min="1"
                        max="50"
                        value={location.radius}
                        onChange={(e) => updateLocationRadius(location.id, parseInt(e.target.value))}
                        className="w-full h-2 bg-gray-700 rounded-lg appearance-none cursor-pointer slider"
                      />
                      <div className="flex justify-between text-xs text-gray-500 mt-1">
                        <span>1 كم</span>
                        <span>50 كم</span>
                      </div>
                    </div>
                    
                    <div className="grid grid-cols-3 gap-2 text-center">
                      <div className="bg-gray-700/50 rounded-lg p-2">
                        <div className="text-orange-400 font-bold text-sm">
                          {Math.floor(Math.PI * location.radius * location.radius)} كم²
                        </div>
                        <div className="text-gray-400 text-xs">المساحة</div>
                      </div>
                      <div className="bg-gray-700/50 rounded-lg p-2">
                        <div className="text-blue-400 font-bold text-sm">
                          {formatNumber(Math.floor((location.population || 100000) * (location.radius / 10)))}
                        </div>
                        <div className="text-gray-400 text-xs">الجمهور المقدر</div>
                      </div>
                      <div className="bg-gray-700/50 rounded-lg p-2">
                        <div className="text-green-400 font-bold text-sm">
                          {(location.radius * 0.5).toFixed(1)} ر.س
                        </div>
                        <div className="text-gray-400 text-xs">تكلفة مقدرة</div>
                      </div>
                    </div>
                  </div>
                </motion.div>
              ))
            )}
          </div>
        </div>
      </div>

      {/* Location Statistics */}
      {campaignData.locations.length > 0 && (
        <div className="bg-gradient-to-br from-gray-900/80 to-purple-900/40 rounded-2xl p-8 backdrop-blur-sm border border-purple-500/20">
          <div className="flex items-center mb-6">
            <BarChart3 className="w-6 h-6 text-purple-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">إحصائيات المواقع المختارة</h3>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-orange-400 mb-2">
                {campaignData.locations.length}
              </div>
              <div className="text-gray-300">عدد المواقع</div>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-blue-400 mb-2">
                {formatNumber(campaignData.locations.reduce((sum, loc) => 
                  sum + Math.floor((loc.population || 100000) * (loc.radius / 10)), 0
                ))}
              </div>
              <div className="text-gray-300">إجمالي الجمهور المقدر</div>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-green-400 mb-2">
                {Math.floor(campaignData.locations.reduce((sum, loc) => 
                  sum + Math.PI * loc.radius * loc.radius, 0
                ))} كم²
              </div>
              <div className="text-gray-300">إجمالي المساحة المغطاة</div>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6 text-center">
              <div className="text-3xl font-bold text-purple-400 mb-2">
                {(campaignData.locations.reduce((sum, loc) => sum + loc.radius * 0.5, 0)).toFixed(1)} ر.س
              </div>
              <div className="text-gray-300">التكلفة المقدرة يومياً</div>
            </div>
          </div>
        </div>
      )}
    </motion.div>
  );


  // ----------------------------------------
  // STEP 4: نوع الحملة والاستراتيجية
  // ----------------------------------------
  
  const renderStep4 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-purple-900/50 to-pink-900/50 p-8 backdrop-blur-sm border border-purple-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-purple-600/10 to-pink-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-purple-500 to-pink-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <Rocket className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text text-transparent">
            نوع الحملة والاستراتيجية
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            اختر نوع الحملة الإعلانية المناسب لأهدافك مع استراتيجيات متطورة لتحقيق أفضل النتائج
          </p>
        </motion.div>
      </div>

      {/* Campaign Goal Selection */}
      <div className="bg-gradient-to-br from-gray-900/80 to-purple-900/40 rounded-2xl p-8 backdrop-blur-sm border border-purple-500/20">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center">
            <Target className="w-6 h-6 text-purple-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">ما هدف حملتك؟</h3>
          </div>
          <motion.button
            onClick={() => setShowGoalModal(true)}
            className="px-4 py-2 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg font-bold hover:from-purple-700 hover:to-pink-700 transition-all duration-300"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            اختر الهدف
          </motion.button>
        </div>
        
        {selectedGoal ? (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-gray-800/50 rounded-xl p-6 border border-purple-500/30"
          >
            {(() => {
              const goal = CAMPAIGN_GOALS.find(g => g.id === selectedGoal);
              return goal ? (
                <div className="flex items-start space-x-4 rtl:space-x-reverse">
                  <div className={`p-3 rounded-full bg-gradient-to-r ${goal.gradient} shadow-lg`}>
                    {goal.icon}
                  </div>
                  <div className="flex-1">
                    <h4 className="text-xl font-bold text-white mb-2">{goal.name}</h4>
                    <p className="text-gray-300 mb-4">{goal.description}</p>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <h5 className="text-sm font-bold text-purple-400 mb-2">المتطلبات:</h5>
                        <ul className="space-y-1">
                          {goal.requirements.map((req, index) => (
                            <li key={index} className="text-sm text-gray-300 flex items-center">
                              <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                              {req}
                            </li>
                          ))}
                        </ul>
                      </div>
                      <div>
                        <h5 className="text-sm font-bold text-purple-400 mb-2">الأنسب لـ:</h5>
                        <ul className="space-y-1">
                          {goal.bestFor.map((item, index) => (
                            <li key={index} className="text-sm text-gray-300 flex items-center">
                              <Star className="w-4 h-4 text-yellow-400 ml-2" />
                              {item}
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              ) : null;
            })()}
          </motion.div>
        ) : (
          <div className="text-center py-8">
            <Target className="w-16 h-16 text-gray-600 mx-auto mb-4" />
            <p className="text-gray-400 text-lg">اختر هدف حملتك لتخصيص الاستراتيجية</p>
          </div>
        )}
      </div>

      {/* Campaign Types */}
      <div className="bg-gradient-to-br from-gray-900/80 to-blue-900/40 rounded-2xl p-8 backdrop-blur-sm border border-blue-500/20">
        <div className="flex items-center mb-6">
          <Rocket className="w-6 h-6 text-blue-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">أنواع الحملات الإعلانية</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {Object.values(GOOGLE_ADS_CAMPAIGN_TYPES).map((type) => (
            <motion.div
              key={type.id}
              className={`relative overflow-hidden rounded-xl p-6 cursor-pointer transition-all duration-300 border-2 ${
                selectedCampaignType === type.id
                  ? `border-${type.color}-500 bg-${type.color}-600/20`
                  : 'border-gray-600 bg-gray-800/50 hover:border-gray-500'
              }`}
              whileHover={{ scale: 1.02, y: -2 }}
              whileTap={{ scale: 0.98 }}
              onClick={() => handleCampaignTypeSelect(type.id)}
            >
              <div className={`absolute inset-0 bg-gradient-to-br ${type.gradient} opacity-10`} />
              
              <div className="relative z-10">
                <div className="flex items-center mb-4">
                  <div className={`p-3 rounded-full bg-gradient-to-r ${type.gradient} shadow-lg`}>
                    {type.icon}
                  </div>
                  <div className="mr-4">
                    <h4 className="text-lg font-bold text-white">{type.name}</h4>
                    {selectedCampaignType === type.id && (
                      <CheckCircle className="w-5 h-5 text-green-400 mt-1" />
                    )}
                  </div>
                </div>
                
                <p className="text-gray-300 text-sm mb-4 leading-relaxed">
                  {type.description}
                </p>
                
                <div className="space-y-3">
                  <div>
                    <h5 className="text-sm font-bold text-white mb-2">الميزات:</h5>
                    <ul className="space-y-1">
                      {type.features.map((feature, index) => (
                        <li key={index} className="text-xs text-gray-300 flex items-center">
                          <Zap className="w-3 h-3 text-yellow-400 ml-1" />
                          {feature}
                        </li>
                      ))}
                    </ul>
                  </div>
                  
                  <div>
                    <h5 className="text-sm font-bold text-white mb-2">الأنسب لـ:</h5>
                    <ul className="space-y-1">
                      {type.bestFor.map((item, index) => (
                        <li key={index} className="text-xs text-gray-300 flex items-center">
                          <Star className="w-3 h-3 text-blue-400 ml-1" />
                          {item}
                        </li>
                      ))}
                    </ul>
                  </div>
                  
                  <div>
                    <h5 className="text-sm font-bold text-white mb-2">المتطلبات:</h5>
                    <ul className="space-y-1">
                      {type.requirements.map((req, index) => (
                        <li key={index} className="text-xs text-gray-300 flex items-center">
                          <CheckCircle className="w-3 h-3 text-green-400 ml-1" />
                          {req}
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>

      {/* AI Recommendations */}
      {selectedCampaignType && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="bg-gradient-to-br from-gray-900/80 to-green-900/40 rounded-2xl p-8 backdrop-blur-sm border border-green-500/20"
        >
          <div className="flex items-center mb-6">
            <Bot className="w-6 h-6 text-green-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">توصيات الذكاء الاصطناعي</h3>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="bg-gray-800/50 rounded-xl p-6">
              <h4 className="text-lg font-bold text-white mb-4 flex items-center">
                <Lightbulb className="w-5 h-5 text-yellow-400 ml-2" />
                نصائح للتحسين
              </h4>
              <ul className="space-y-2">
                <li className="text-gray-300 text-sm flex items-start">
                  <CheckCircle className="w-4 h-4 text-green-400 ml-2 mt-0.5" />
                  استخدم كلمات مفتاحية طويلة لتقليل التكلفة
                </li>
                <li className="text-gray-300 text-sm flex items-start">
                  <CheckCircle className="w-4 h-4 text-green-400 ml-2 mt-0.5" />
                  اختبر عدة إعلانات مختلفة لتحسين الأداء
                </li>
                <li className="text-gray-300 text-sm flex items-start">
                  <CheckCircle className="w-4 h-4 text-green-400 ml-2 mt-0.5" />
                  راقب الأداء يومياً وعدل الميزانية حسب الحاجة
                </li>
                <li className="text-gray-300 text-sm flex items-start">
                  <CheckCircle className="w-4 h-4 text-green-400 ml-2 mt-0.5" />
                  استهدف الأوقات التي يكون فيها جمهورك أكثر نشاطاً
                </li>
              </ul>
            </div>
            
            <div className="bg-gray-800/50 rounded-xl p-6">
              <h4 className="text-lg font-bold text-white mb-4 flex items-center">
                <TrendingUp className="w-5 h-5 text-blue-400 ml-2" />
                توقعات الأداء
              </h4>
              <div className="space-y-3">
                <div className="flex justify-between items-center">
                  <span className="text-gray-300 text-sm">معدل النقر المتوقع:</span>
                  <span className="text-green-400 font-bold">2.5% - 4.2%</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-300 text-sm">معدل التحويل المتوقع:</span>
                  <span className="text-blue-400 font-bold">1.8% - 3.5%</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-300 text-sm">تكلفة التحويل المتوقعة:</span>
                  <span className="text-purple-400 font-bold">45 - 85 ر.س</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-300 text-sm">العائد على الاستثمار:</span>
                  <span className="text-yellow-400 font-bold">250% - 400%</span>
                </div>
              </div>
            </div>
          </div>
        </motion.div>
      )}
    </motion.div>
  );

  // ----------------------------------------
  // STEP 5: الميزانية الذكية والجدولة
  // ----------------------------------------
  
  const renderStep5 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-yellow-900/50 to-orange-900/50 p-8 backdrop-blur-sm border border-yellow-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-yellow-600/10 to-orange-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-yellow-500 to-orange-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <DollarSign className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-yellow-400 to-orange-400 bg-clip-text text-transparent">
            الميزانية الذكية والجدولة
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            حدد ميزانيتك وجدولة عرض إعلاناتك بذكاء لتحقيق أفضل عائد على الاستثمار
          </p>
        </motion.div>
      </div>

      {/* Budget Settings */}
      <div className="bg-gradient-to-br from-gray-900/80 to-yellow-900/40 rounded-2xl p-8 backdrop-blur-sm border border-yellow-500/20">
        <div className="flex items-center mb-6">
          <DollarSign className="w-6 h-6 text-yellow-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">إعدادات الميزانية</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Budget Type */}
          <div className="space-y-4">
            <h4 className="text-lg font-bold text-white">نوع الميزانية</h4>
            <div className="space-y-3">
              {[
                { id: 'daily', name: 'ميزانية يومية', description: 'مبلغ ثابت يومياً', icon: <Calendar className="w-5 h-5" /> },
                { id: 'total', name: 'ميزانية إجمالية', description: 'مبلغ إجمالي للحملة', icon: <DollarSign className="w-5 h-5" /> }
              ].map((type) => (
                <motion.div
                  key={type.id}
                  className={`p-4 rounded-xl border-2 cursor-pointer transition-all duration-300 ${
                    campaignData.budget.type === type.id
                      ? 'border-yellow-500 bg-yellow-600/20'
                      : 'border-gray-600 bg-gray-800/50 hover:border-yellow-400'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => setCampaignData(prev => ({
                    ...prev,
                    budget: { ...prev.budget, type: type.id as 'daily' | 'total' }
                  }))}
                >
                  <div className="flex items-center space-x-3 rtl:space-x-reverse">
                    <div className="text-yellow-400">{type.icon}</div>
                    <div className="flex-1">
                      <h5 className="text-white font-bold">{type.name}</h5>
                      <p className="text-gray-400 text-sm">{type.description}</p>
                    </div>
                    {campaignData.budget.type === type.id && (
                      <CheckCircle className="w-5 h-5 text-green-400" />
                    )}
                  </div>
                </motion.div>
              ))}
            </div>
          </div>
          
          {/* Budget Amount */}
          <div className="space-y-4">
            <h4 className="text-lg font-bold text-white">المبلغ</h4>
            <div className="space-y-4">
              <div className="relative">
                <input
                  type="number"
                  value={campaignData.budget.amount}
                  onChange={(e) => setCampaignData(prev => ({
                    ...prev,
                    budget: { ...prev.budget, amount: parseInt(e.target.value) || 0 }
                  }))}
                  className="w-full px-6 py-4 bg-gray-800/50 border border-gray-600 rounded-xl text-white text-2xl font-bold text-center focus:outline-none focus:ring-2 focus:ring-yellow-500 focus:border-transparent transition-all duration-300"
                  min="10"
                  max="100000"
                />
                <div className="absolute left-4 top-1/2 transform -translate-y-1/2 text-yellow-400 font-bold">
                  ر.س
                </div>
              </div>
              
              <div className="grid grid-cols-3 gap-2">
                {[100, 500, 1000].map((amount) => (
                  <motion.button
                    key={amount}
                    onClick={() => setCampaignData(prev => ({
                      ...prev,
                      budget: { ...prev.budget, amount }
                    }))}
                    className="py-2 px-4 bg-gray-700 text-white rounded-lg hover:bg-yellow-600 transition-all duration-300 text-sm font-bold"
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    {amount} ر.س
                  </motion.button>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Schedule Settings */}
      <div className="bg-gradient-to-br from-gray-900/80 to-orange-900/40 rounded-2xl p-8 backdrop-blur-sm border border-orange-500/20">
        <div className="flex items-center mb-6">
          <Clock className="w-6 h-6 text-orange-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">جدولة عرض الإعلانات</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mb-6">
          {SCHEDULE_OPTIONS.map((option) => (
            <motion.div
              key={option.id}
              className={`p-4 rounded-xl border-2 cursor-pointer transition-all duration-300 ${
                campaignData.schedule.type === option.id
                  ? `border-${option.color}-500 bg-${option.color}-600/20`
                  : 'border-gray-600 bg-gray-800/50 hover:border-gray-500'
              }`}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={() => setCampaignData(prev => ({
                ...prev,
                schedule: { ...prev.schedule, type: option.id }
              }))}
            >
              <div className="flex items-center space-x-3 rtl:space-x-reverse mb-2">
                <div className={`text-${option.color}-400`}>{option.icon}</div>
                <h4 className="text-white font-bold">{option.name}</h4>
                {campaignData.schedule.type === option.id && (
                  <CheckCircle className="w-5 h-5 text-green-400 mr-auto" />
                )}
              </div>
              <p className="text-gray-400 text-sm">{option.description}</p>
            </motion.div>
          ))}
        </div>
        
        {/* Custom Schedule */}
        {campaignData.schedule.type === 'custom' && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className="space-y-4"
          >
            <div className="flex items-center justify-between">
              <h4 className="text-lg font-bold text-white">الجدولة المخصصة</h4>
              <motion.button
                onClick={addTimeSlot}
                className="px-4 py-2 bg-gradient-to-r from-orange-600 to-red-600 text-white rounded-lg font-bold hover:from-orange-700 hover:to-red-700 transition-all duration-300 flex items-center"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Plus className="w-4 h-4 ml-2" />
                إضافة فترة زمنية
              </motion.button>
            </div>
            
            <div className="space-y-4">
              {campaignData.schedule.customSlots.map((slot) => (
                <motion.div
                  key={slot.id}
                  className="bg-gray-800/50 rounded-xl p-4 border border-gray-600/50"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                >
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">من</label>
                      <input
                        type="time"
                        value={slot.startTime}
                        onChange={(e) => updateTimeSlot(slot.id, { startTime: e.target.value })}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      />
                    </div>
                    
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-2">إلى</label>
                      <input
                        type="time"
                        value={slot.endTime}
                        onChange={(e) => updateTimeSlot(slot.id, { endTime: e.target.value })}
                        className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-orange-500"
                      />
                    </div>
                    
                    <div className="flex items-end">
                      <motion.button
                        onClick={() => removeTimeSlot(slot.id)}
                        className="w-full py-2 px-4 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-all duration-300 flex items-center justify-center"
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                      >
                        <Trash2 className="w-4 h-4 ml-2" />
                        حذف
                      </motion.button>
                    </div>
                  </div>
                  
                  <div className="mt-4">
                    <label className="block text-sm font-medium text-gray-300 mb-2">الأيام</label>
                    <div className="grid grid-cols-7 gap-2">
                      {weekDays.map((day) => (
                        <motion.button
                          key={day.id}
                          onClick={() => {
                            const newDays = slot.days.includes(day.id)
                              ? slot.days.filter(d => d !== day.id)
                              : [...slot.days, day.id];
                            updateTimeSlot(slot.id, { days: newDays });
                          }}
                          className={`py-2 px-2 rounded-lg text-xs font-bold transition-all duration-300 ${
                            slot.days.includes(day.id)
                              ? 'bg-orange-600 text-white'
                              : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                          }`}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                        >
                          {day.short}
                        </motion.button>
                      ))}
                    </div>
                  </div>
                </motion.div>
              ))}
            </div>
          </motion.div>
        )}
      </div>

      {/* Performance Predictions */}
      <div className="bg-gradient-to-br from-gray-900/80 to-purple-900/40 rounded-2xl p-8 backdrop-blur-sm border border-purple-500/20">
        <div className="flex items-center mb-6">
          <BarChart3 className="w-6 h-6 text-purple-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">توقعات الأداء</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {Object.entries(calculatePerformancePredictions).map(([scenario, data]) => (
            <motion.div
              key={scenario}
              className={`bg-gray-800/50 rounded-xl p-6 border ${
                scenario === 'conservative' ? 'border-red-500/30' :
                scenario === 'realistic' ? 'border-yellow-500/30' : 'border-green-500/30'
              }`}
              whileHover={{ scale: 1.02 }}
            >
              <div className="text-center mb-4">
                <h4 className={`text-lg font-bold ${
                  scenario === 'conservative' ? 'text-red-400' :
                  scenario === 'realistic' ? 'text-yellow-400' : 'text-green-400'
                }`}>
                  {scenario === 'conservative' ? 'محافظ' :
                   scenario === 'realistic' ? 'واقعي' : 'متفائل'}
                </h4>
              </div>
              
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="text-gray-300 text-sm">الظهور:</span>
                  <span className="text-white font-bold">{formatNumber(data.impressions)}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-300 text-sm">النقرات:</span>
                  <span className="text-blue-400 font-bold">{formatNumber(data.clicks)}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-300 text-sm">التحويلات:</span>
                  <span className="text-green-400 font-bold">{data.conversions}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-300 text-sm">التكلفة:</span>
                  <span className="text-purple-400 font-bold">{formatCurrency(data.cost)}</span>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );


  // ----------------------------------------
  // STEP 6: الأصول الإعلانية والإطلاق
  // ----------------------------------------
  
  const renderStep6 = () => (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="space-y-8"
    >
      {/* Header */}
      <div className="text-center relative overflow-hidden rounded-2xl bg-gradient-to-br from-teal-900/50 to-cyan-900/50 p-8 backdrop-blur-sm border border-teal-500/20">
        <div className="absolute inset-0 bg-gradient-to-r from-teal-600/10 to-cyan-600/10" />
        <div className="absolute inset-0">{generateStars()}</div>
        
        <motion.div
          className="relative z-10"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-center mb-4">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-teal-500 to-cyan-500 shadow-lg"
              whileHover={{ scale: 1.1, rotate: 360 }}
              transition={{ duration: 0.5 }}
            >
              <Upload className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h2 className="text-4xl font-bold mb-4 bg-gradient-to-r from-teal-400 to-cyan-400 bg-clip-text text-transparent">
            الأصول الإعلانية والإطلاق
          </h2>
          
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            أنشئ وارفع الأصول الإعلانية مع اختبار A/B متقدم واستعد لإطلاق حملتك بنجاح
          </p>
        </motion.div>
      </div>

      {/* A/B Testing Ads */}
      <div className="bg-gradient-to-br from-gray-900/80 to-teal-900/40 rounded-2xl p-8 backdrop-blur-sm border border-teal-500/20">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center">
            <BarChart className="w-6 h-6 text-teal-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">اختبار A/B للإعلانات</h3>
          </div>
          <span className="bg-teal-600/20 text-teal-300 px-3 py-1 rounded-full text-sm font-bold">
            3 إعلانات للاختبار
          </span>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {['A', 'B', 'C'].map((variant, index) => (
            <motion.div
              key={variant}
              className="bg-gray-800/50 rounded-xl p-6 border border-gray-600/50"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
            >
              <div className="flex items-center justify-between mb-4">
                <h4 className="text-lg font-bold text-white">إعلان {variant}</h4>
                <div className={`w-8 h-8 rounded-full flex items-center justify-center font-bold text-white ${
                  variant === 'A' ? 'bg-blue-600' : variant === 'B' ? 'bg-green-600' : 'bg-purple-600'
                }`}>
                  {variant}
                </div>
              </div>
              
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">العنوان الرئيسي</label>
                  <input
                    type="text"
                    placeholder="اكتب عنوان جذاب..."
                    className="w-full px-4 py-3 bg-gray-700/50 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-transparent transition-all duration-300"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">الوصف</label>
                  <textarea
                    placeholder="اكتب وصف مقنع..."
                    rows={3}
                    className="w-full px-4 py-3 bg-gray-700/50 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-transparent transition-all duration-300 resize-none"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">الصورة/الفيديو</label>
                  <div className="border-2 border-dashed border-gray-600 rounded-lg p-6 text-center hover:border-teal-500 transition-all duration-300 cursor-pointer">
                    <UploadCloud className="w-8 h-8 text-gray-400 mx-auto mb-2" />
                    <p className="text-gray-400 text-sm">اسحب وأفلت أو انقر للرفع</p>
                    <p className="text-gray-500 text-xs mt-1">PNG, JPG, MP4 حتى 10MB</p>
                  </div>
                </div>
                
                {/* Performance Metrics */}
                <div className="bg-gray-700/30 rounded-lg p-4">
                  <h5 className="text-sm font-bold text-white mb-3">توقعات الأداء</h5>
                  <div className="space-y-2">
                    <div className="flex justify-between text-xs">
                      <span className="text-gray-300">معدل النقر:</span>
                      <span className={`font-bold ${
                        variant === 'A' ? 'text-blue-400' : variant === 'B' ? 'text-green-400' : 'text-purple-400'
                      }`}>
                        {(2.1 + index * 0.3).toFixed(1)}%
                      </span>
                    </div>
                    <div className="flex justify-between text-xs">
                      <span className="text-gray-300">الظهور المتوقع:</span>
                      <span className="text-gray-300 font-bold">
                        {formatNumber(8500 + index * 1200)}
                      </span>
                    </div>
                    <div className="flex justify-between text-xs">
                      <span className="text-gray-300">النقرات المتوقعة:</span>
                      <span className="text-gray-300 font-bold">
                        {Math.floor((8500 + index * 1200) * (2.1 + index * 0.3) / 100)}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Asset Requirements by Campaign Type */}
      {selectedCampaignType && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="bg-gradient-to-br from-gray-900/80 to-blue-900/40 rounded-2xl p-8 backdrop-blur-sm border border-blue-500/20"
        >
          <div className="flex items-center mb-6">
            <FileText className="w-6 h-6 text-blue-400 ml-3" />
            <h3 className="text-2xl font-bold text-white">الأصول المطلوبة لنوع الحملة</h3>
          </div>
          
          {(() => {
            const campaignType = GOOGLE_ADS_CAMPAIGN_TYPES[selectedCampaignType as keyof typeof GOOGLE_ADS_CAMPAIGN_TYPES];
            return campaignType ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {campaignType.requirements.map((requirement, index) => (
                  <motion.div
                    key={index}
                    className="bg-gray-800/50 rounded-xl p-6 border border-gray-600/50"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: index * 0.1 }}
                  >
                    <div className="flex items-center mb-4">
                      {requirement.includes('صور') ? (
                        <ImageIcon className="w-6 h-6 text-blue-400 ml-3" />
                      ) : requirement.includes('فيديو') ? (
                        <VideoIcon className="w-6 h-6 text-red-400 ml-3" />
                      ) : requirement.includes('هاتف') ? (
                        <Phone className="w-6 h-6 text-green-400 ml-3" />
                      ) : (
                        <FileText className="w-6 h-6 text-purple-400 ml-3" />
                      )}
                      <h4 className="text-lg font-bold text-white">{requirement}</h4>
                    </div>
                    
                    <div className="space-y-4">
                      {requirement.includes('صور') && (
                        <div className="border-2 border-dashed border-gray-600 rounded-lg p-4 text-center hover:border-blue-500 transition-all duration-300 cursor-pointer">
                          <ImageIcon className="w-6 h-6 text-gray-400 mx-auto mb-2" />
                          <p className="text-gray-400 text-sm">رفع الصور</p>
                          <p className="text-gray-500 text-xs">1200x628 بكسل مُوصى به</p>
                        </div>
                      )}
                      
                      {requirement.includes('فيديو') && (
                        <div className="border-2 border-dashed border-gray-600 rounded-lg p-4 text-center hover:border-red-500 transition-all duration-300 cursor-pointer">
                          <VideoIcon className="w-6 h-6 text-gray-400 mx-auto mb-2" />
                          <p className="text-gray-400 text-sm">رفع الفيديو</p>
                          <p className="text-gray-500 text-xs">MP4, MOV حتى 100MB</p>
                        </div>
                      )}
                      
                      {requirement.includes('هاتف') && (
                        <div className="space-y-2">
                          <input
                            type="tel"
                            placeholder="+966 50 123 4567"
                            className="w-full px-4 py-3 bg-gray-700/50 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent transition-all duration-300"
                            dir="ltr"
                          />
                          <p className="text-gray-500 text-xs">رقم الهاتف مع رمز الدولة</p>
                        </div>
                      )}
                      
                      {!requirement.includes('صور') && !requirement.includes('فيديو') && !requirement.includes('هاتف') && (
                        <textarea
                          placeholder={`اكتب ${requirement}...`}
                          rows={3}
                          className="w-full px-4 py-3 bg-gray-700/50 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-300 resize-none"
                        />
                      )}
                    </div>
                  </motion.div>
                ))}
              </div>
            ) : null;
          })()}
        </motion.div>
      )}

      {/* Audience Statistics */}
      <div className="bg-gradient-to-br from-gray-900/80 to-purple-900/40 rounded-2xl p-8 backdrop-blur-sm border border-purple-500/20">
        <div className="flex items-center mb-6">
          <Users className="w-6 h-6 text-purple-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">إحصائيات الجمهور المستهدف</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {/* Age Groups */}
          <div className="bg-gray-800/50 rounded-xl p-6">
            <h4 className="text-lg font-bold text-white mb-4 flex items-center">
              <UserCheck className="w-5 h-5 text-blue-400 ml-2" />
              الفئات العمرية
            </h4>
            <div className="space-y-3">
              {campaignData.audienceData.ageGroups.map((group) => (
                <div key={group.id} className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      checked={group.selected}
                      onChange={(e) => {
                        setCampaignData(prev => ({
                          ...prev,
                          audienceData: {
                            ...prev.audienceData,
                            ageGroups: prev.audienceData.ageGroups.map(g =>
                              g.id === group.id ? { ...g, selected: e.target.checked } : g
                            )
                          }
                        }));
                      }}
                      className="w-4 h-4 text-blue-600 bg-gray-700 border-gray-600 rounded focus:ring-blue-500"
                    />
                    <span className="text-white text-sm mr-3">{group.label}</span>
                  </div>
                  <span className="text-blue-400 text-sm font-bold">{group.percentage}%</span>
                </div>
              ))}
            </div>
          </div>
          
          {/* Genders */}
          <div className="bg-gray-800/50 rounded-xl p-6">
            <h4 className="text-lg font-bold text-white mb-4 flex items-center">
              <Users className="w-5 h-5 text-pink-400 ml-2" />
              الجنس
            </h4>
            <div className="space-y-3">
              {campaignData.audienceData.genders.map((gender) => (
                <div key={gender.id} className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      checked={gender.selected}
                      onChange={(e) => {
                        setCampaignData(prev => ({
                          ...prev,
                          audienceData: {
                            ...prev.audienceData,
                            genders: prev.audienceData.genders.map(g =>
                              g.id === gender.id ? { ...g, selected: e.target.checked } : g
                            )
                          }
                        }));
                      }}
                      className="w-4 h-4 text-pink-600 bg-gray-700 border-gray-600 rounded focus:ring-pink-500"
                    />
                    <span className="text-white text-sm mr-3">{gender.label}</span>
                  </div>
                  <span className="text-pink-400 text-sm font-bold">{gender.percentage}%</span>
                </div>
              ))}
            </div>
            
            {/* Gender Chart */}
            <div className="mt-6 h-32">
              <Doughnut
                data={{
                  labels: campaignData.audienceData.genders.filter(g => g.selected).map(g => g.label),
                  datasets: [{
                    data: campaignData.audienceData.genders.filter(g => g.selected).map(g => g.percentage),
                    backgroundColor: ['#3B82F6', '#EC4899'],
                    borderWidth: 0
                  }]
                }}
                options={{
                  responsive: true,
                  maintainAspectRatio: false,
                  plugins: {
                    legend: {
                      display: false
                    }
                  }
                }}
              />
            </div>
          </div>
          
          {/* Interests */}
          <div className="bg-gray-800/50 rounded-xl p-6">
            <h4 className="text-lg font-bold text-white mb-4 flex items-center">
              <Star className="w-5 h-5 text-yellow-400 ml-2" />
              الاهتمامات
            </h4>
            <div className="space-y-3">
              {campaignData.audienceData.interests.map((interest) => (
                <div key={interest.id} className="flex items-center justify-between">
                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      checked={interest.selected}
                      onChange={(e) => {
                        setCampaignData(prev => ({
                          ...prev,
                          audienceData: {
                            ...prev.audienceData,
                            interests: prev.audienceData.interests.map(i =>
                              i.id === interest.id ? { ...i, selected: e.target.checked } : i
                            )
                          }
                        }));
                      }}
                      className="w-4 h-4 text-yellow-600 bg-gray-700 border-gray-600 rounded focus:ring-yellow-500"
                    />
                    <span className="text-white text-sm mr-3">{interest.label}</span>
                  </div>
                  <span className="text-yellow-400 text-sm font-bold">{interest.percentage}%</span>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Campaign Summary */}
      <div className="bg-gradient-to-br from-gray-900/80 to-green-900/40 rounded-2xl p-8 backdrop-blur-sm border border-green-500/20">
        <div className="flex items-center mb-6">
          <CheckCircle className="w-6 h-6 text-green-400 ml-3" />
          <h3 className="text-2xl font-bold text-white">ملخص الحملة</h3>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <div className="bg-gray-800/50 rounded-xl p-6 text-center">
            <div className="text-3xl font-bold text-blue-400 mb-2">
              {campaignData.keywords.length}
            </div>
            <div className="text-gray-300 text-sm">كلمة مفتاحية</div>
          </div>
          
          <div className="bg-gray-800/50 rounded-xl p-6 text-center">
            <div className="text-3xl font-bold text-orange-400 mb-2">
              {campaignData.locations.length}
            </div>
            <div className="text-gray-300 text-sm">موقع مستهدف</div>
          </div>
          
          <div className="bg-gray-800/50 rounded-xl p-6 text-center">
            <div className="text-3xl font-bold text-green-400 mb-2">
              {formatCurrency(campaignData.budget.amount)}
            </div>
            <div className="text-gray-300 text-sm">
              ميزانية {campaignData.budget.type === 'daily' ? 'يومية' : 'إجمالية'}
            </div>
          </div>
          
          <div className="bg-gray-800/50 rounded-xl p-6 text-center">
            <div className="text-3xl font-bold text-purple-400 mb-2">
              {campaignData.selectedLanguages.length}
            </div>
            <div className="text-gray-300 text-sm">لغة مستهدفة</div>
          </div>
        </div>
      </div>

      {/* Launch Campaign */}
      <div className="bg-gradient-to-br from-gray-900/80 to-red-900/40 rounded-2xl p-8 backdrop-blur-sm border border-red-500/20">
        <div className="text-center">
          <div className="flex items-center justify-center mb-6">
            <motion.div
              className="p-4 rounded-full bg-gradient-to-r from-red-500 to-pink-500 shadow-lg"
              whileHover={{ scale: 1.1 }}
              transition={{ duration: 0.3 }}
            >
              <Rocket className="w-8 h-8 text-white" />
            </motion.div>
          </div>
          
          <h3 className="text-3xl font-bold text-white mb-4">جاهز للإطلاق!</h3>
          <p className="text-xl text-gray-300 mb-8 max-w-2xl mx-auto">
            تم إعداد حملتك الإعلانية بنجاح. انقر على الزر أدناه لإطلاق الحملة وبدء الوصول إلى جمهورك المستهدف
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <motion.button
              className="px-8 py-4 bg-gradient-to-r from-red-600 to-pink-600 text-white rounded-xl font-bold text-lg hover:from-red-700 hover:to-pink-700 transition-all duration-300 shadow-lg hover:shadow-xl flex items-center justify-center"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Rocket className="w-6 h-6 ml-3" />
              إطلاق الحملة الآن
            </motion.button>
            
            <motion.button
              className="px-8 py-4 bg-gray-700 text-white rounded-xl font-bold text-lg hover:bg-gray-600 transition-all duration-300 flex items-center justify-center"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Eye className="w-6 h-6 ml-3" />
              معاينة الحملة
            </motion.button>
          </div>
        </div>
      </div>
    </motion.div>
  );

  // ----------------------------------------
  // MODALS
  // ----------------------------------------

  const renderAnalysisModal = () => (
    <AnimatePresence>
      {showAnalysisModal && (
        <motion.div
          className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <motion.div
            className="bg-gradient-to-br from-gray-900 to-blue-900 rounded-2xl p-8 max-w-md w-full border border-blue-500/30"
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.8, opacity: 0 }}
          >
            <div className="text-center">
              <div className="flex items-center justify-center mb-6">
                <motion.div
                  className="p-4 rounded-full bg-gradient-to-r from-blue-500 to-purple-500 shadow-lg"
                  animate={{ rotate: 360 }}
                  transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
                >
                  <Brain className="w-8 h-8 text-white" />
                </motion.div>
              </div>
              
              <h3 className="text-2xl font-bold text-white mb-4">تحليل الموقع بالذكاء الاصطناعي</h3>
              
              <div className="mb-6">
                <div className="w-full bg-gray-700 rounded-full h-3 mb-4">
                  <motion.div
                    className="bg-gradient-to-r from-blue-500 to-purple-500 h-3 rounded-full"
                    initial={{ width: 0 }}
                    animate={{ width: `${analysisProgress}%` }}
                    transition={{ duration: 0.5 }}
                  />
                </div>
                <p className="text-gray-300">{analysisProgress}% مكتمل</p>
              </div>
              
              <div className="space-y-2 text-sm text-gray-300">
                {analysisProgress >= 20 && (
                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="flex items-center"
                  >
                    <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                    فحص بنية الموقع...
                  </motion.p>
                )}
                {analysisProgress >= 40 && (
                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="flex items-center"
                  >
                    <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                    استخراج الكلمات المفتاحية...
                  </motion.p>
                )}
                {analysisProgress >= 60 && (
                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="flex items-center"
                  >
                    <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                    تحليل المحتوى...
                  </motion.p>
                )}
                {analysisProgress >= 80 && (
                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="flex items-center"
                  >
                    <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                    تحليل الجمهور المستهدف...
                  </motion.p>
                )}
                {analysisProgress >= 100 && (
                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="flex items-center text-green-400 font-bold"
                  >
                    <CheckCircle className="w-4 h-4 text-green-400 ml-2" />
                    اكتمل التحليل!
                  </motion.p>
                )}
              </div>
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );

  const renderGoalModal = () => (
    <AnimatePresence>
      {showGoalModal && (
        <motion.div
          className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={() => setShowGoalModal(false)}
        >
          <motion.div
            className="bg-gradient-to-br from-gray-900 to-purple-900 rounded-2xl p-8 max-w-4xl w-full max-h-[90vh] overflow-y-auto border border-purple-500/30"
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.8, opacity: 0 }}
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-2xl font-bold text-white">اختر هدف حملتك</h3>
              <motion.button
                onClick={() => setShowGoalModal(false)}
                className="p-2 text-gray-400 hover:text-white hover:bg-gray-700 rounded-lg transition-all duration-300"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
              >
                <X className="w-6 h-6" />
              </motion.button>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {CAMPAIGN_GOALS.map((goal) => (
                <motion.div
                  key={goal.id}
                  className="bg-gray-800/50 rounded-xl p-6 cursor-pointer border border-gray-600/50 hover:border-purple-500/50 transition-all duration-300"
                  whileHover={{ scale: 1.02, y: -2 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => handleGoalSelect(goal.id)}
                >
                  <div className="flex items-start space-x-4 rtl:space-x-reverse">
                    <div className={`p-3 rounded-full bg-gradient-to-r ${goal.gradient} shadow-lg`}>
                      {goal.icon}
                    </div>
                    <div className="flex-1">
                      <h4 className="text-lg font-bold text-white mb-2">{goal.name}</h4>
                      <p className="text-gray-300 text-sm mb-4">{goal.description}</p>
                      
                      <div className="space-y-2">
                        <div>
                          <h5 className="text-xs font-bold text-purple-400 mb-1">الأنسب لـ:</h5>
                          <div className="flex flex-wrap gap-1">
                            {goal.bestFor.slice(0, 2).map((item, index) => (
                              <span
                                key={index}
                                className="px-2 py-1 bg-purple-600/20 text-purple-300 rounded-full text-xs"
                              >
                                {item}
                              </span>
                            ))}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </motion.div>
              ))}
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );

  const renderRequirementsModal = () => (
    <AnimatePresence>
      {showRequirementsModal && (
        <motion.div
          className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={() => setShowRequirementsModal(false)}
        >
          <motion.div
            className="bg-gradient-to-br from-gray-900 to-green-900 rounded-2xl p-8 max-w-2xl w-full border border-green-500/30"
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.8, opacity: 0 }}
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-2xl font-bold text-white">متطلبات الحملة</h3>
              <motion.button
                onClick={() => setShowRequirementsModal(false)}
                className="p-2 text-gray-400 hover:text-white hover:bg-gray-700 rounded-lg transition-all duration-300"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
              >
                <X className="w-6 h-6" />
              </motion.button>
            </div>
            
            <div className="space-y-4">
              <p className="text-gray-300">لإنشاء حملة ناجحة، ستحتاج إلى الأصول التالية:</p>
              
              <ul className="space-y-3">
                {currentRequirements.map((requirement, index) => (
                  <motion.li
                    key={index}
                    className="flex items-center text-gray-300"
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: index * 0.1 }}
                  >
                    <CheckCircle className="w-5 h-5 text-green-400 ml-3" />
                    {requirement}
                  </motion.li>
                ))}
              </ul>
              
              <div className="mt-6 pt-6 border-t border-gray-700">
                <motion.button
                  onClick={() => setShowRequirementsModal(false)}
                  className="w-full py-3 px-6 bg-gradient-to-r from-green-600 to-blue-600 text-white rounded-xl font-bold hover:from-green-700 hover:to-blue-700 transition-all duration-300"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  فهمت، لنبدأ!
                </motion.button>
              </div>
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );

  // ----------------------------------------
  // MAIN RENDER
  // ----------------------------------------

  if (loadError) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900 flex items-center justify-center">
        <div className="text-center">
          <AlertCircle className="w-16 h-16 text-red-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-white mb-2">خطأ في تحميل الخرائط</h2>
          <p className="text-gray-300">يرجى التحقق من مفتاح Google Maps API</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900 relative overflow-hidden">
      {/* Background Effects */}
      <div className="absolute inset-0 bg-gradient-to-br from-blue-900/20 via-purple-900/20 to-pink-900/20" />
      <div className="absolute inset-0">{generateStars()}</div>
      
      {/* Main Content */}
      <div className="relative z-10 container mx-auto px-4 py-8">
        {/* Header */}
        <motion.div
          className="text-center mb-12"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
        >
          <h1 className="text-5xl font-bold mb-4 bg-gradient-to-r from-blue-400 via-purple-400 to-pink-400 bg-clip-text text-transparent">
            إنشاء حملة إعلانية جديدة
          </h1>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            أنشئ حملة إعلانية احترافية بالذكاء الاصطناعي مع أحدث التقنيات والتصاميم المبهرة
          </p>
        </motion.div>

        {/* Step Indicator */}
        {renderStepIndicator()}

        {/* Step Content */}
        <AnimatePresence mode="wait">
          {currentStep === 1 && renderStep1()}
          {currentStep === 2 && renderStep2()}
          {currentStep === 3 && renderStep3()}
          {currentStep === 4 && renderStep4()}
          {currentStep === 5 && renderStep5()}
          {currentStep === 6 && renderStep6()}
        </AnimatePresence>

        {/* Navigation */}
        <div className="flex justify-between items-center mt-12">
          <motion.button
            onClick={prevStep}
            disabled={currentStep === 1}
            className="px-6 py-3 bg-gray-700 text-white rounded-xl font-bold disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-600 transition-all duration-300 flex items-center"
            whileHover={{ scale: currentStep > 1 ? 1.05 : 1 }}
            whileTap={{ scale: currentStep > 1 ? 0.95 : 1 }}
          >
            <ChevronLeft className="w-5 h-5 ml-2" />
            السابق
          </motion.button>

          <div className="text-center">
            <span className="text-gray-400">الخطوة {currentStep} من 6</span>
          </div>

          <motion.button
            onClick={nextStep}
            disabled={currentStep === 6}
            className="px-6 py-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl font-bold disabled:opacity-50 disabled:cursor-not-allowed hover:from-blue-700 hover:to-purple-700 transition-all duration-300 flex items-center"
            whileHover={{ scale: currentStep < 6 ? 1.05 : 1 }}
            whileTap={{ scale: currentStep < 6 ? 0.95 : 1 }}
          >
            التالي
            <ChevronRight className="w-5 h-5 mr-2" />
          </motion.button>
        </div>
      </div>

      {/* Modals */}
      {renderAnalysisModal()}
      {renderGoalModal()}
      {renderRequirementsModal()}
    </div>
  );
};

// export default NewCampaignMasterFinal;


// ----------------------------------------
// CUSTOM CSS STYLES
// ----------------------------------------

const customStyles = `
  /* Custom Scrollbar */
  .custom-scrollbar::-webkit-scrollbar {
    width: 8px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-track {
    background: rgba(31, 41, 55, 0.5);
    border-radius: 4px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-thumb {
    background: linear-gradient(45deg, #3B82F6, #8B5CF6);
    border-radius: 4px;
  }
  
  .custom-scrollbar::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(45deg, #2563EB, #7C3AED);
  }

  /* Glassmorphism Effect */
  .glass-effect {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  }

  /* Gradient Text Animation */
  .gradient-text-animated {
    background: linear-gradient(-45deg, #3B82F6, #8B5CF6, #EC4899, #F59E0B);
    background-size: 400% 400%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: gradientShift 4s ease infinite;
  }

  @keyframes gradientShift {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }

  /* Floating Animation */
  .floating {
    animation: floating 3s ease-in-out infinite;
  }

  @keyframes floating {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
  }

  /* Pulse Glow Effect */
  .pulse-glow {
    animation: pulseGlow 2s ease-in-out infinite alternate;
  }

  @keyframes pulseGlow {
    from {
      box-shadow: 0 0 20px rgba(59, 130, 246, 0.5);
    }
    to {
      box-shadow: 0 0 30px rgba(139, 92, 246, 0.8), 0 0 40px rgba(236, 72, 153, 0.3);
    }
  }

  /* Neural Network Animation */
  .neural-network {
    position: relative;
    overflow: hidden;
  }

  .neural-network::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
      radial-gradient(circle at 20% 20%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 80% 80%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 40% 60%, rgba(236, 72, 153, 0.1) 0%, transparent 50%);
    animation: neuralPulse 6s ease-in-out infinite;
  }

  @keyframes neuralPulse {
    0%, 100% { opacity: 0.3; transform: scale(1); }
    50% { opacity: 0.6; transform: scale(1.1); }
  }

  /* Holographic Border */
  .holographic-border {
    position: relative;
    background: linear-gradient(45deg, transparent, rgba(59, 130, 246, 0.1), transparent);
    border: 1px solid transparent;
  }

  .holographic-border::before {
    content: '';
    position: absolute;
    inset: 0;
    padding: 2px;
    background: linear-gradient(45deg, #3B82F6, #8B5CF6, #EC4899, #F59E0B, #3B82F6);
    border-radius: inherit;
    mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    mask-composite: exclude;
    animation: holographicRotate 3s linear infinite;
  }

  @keyframes holographicRotate {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  /* Particle System */
  .particle-system {
    position: relative;
    overflow: hidden;
  }

  .particle {
    position: absolute;
    width: 2px;
    height: 2px;
    background: radial-gradient(circle, rgba(59, 130, 246, 0.8) 0%, transparent 70%);
    border-radius: 50%;
    animation: particleFloat 8s linear infinite;
  }

  @keyframes particleFloat {
    0% {
      transform: translateY(100vh) translateX(0);
      opacity: 0;
    }
    10% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      transform: translateY(-100px) translateX(100px);
      opacity: 0;
    }
  }

  /* Morphing Shapes */
  .morphing-shape {
    background: linear-gradient(45deg, #3B82F6, #8B5CF6);
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    animation: morphing 8s ease-in-out infinite;
  }

  @keyframes morphing {
    0%, 100% {
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      transform: rotate(0deg);
    }
    25% {
      border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
      transform: rotate(90deg);
    }
    50% {
      border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
      transform: rotate(180deg);
    }
    75% {
      border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
      transform: rotate(270deg);
    }
  }

  /* Liquid Button Effect */
  .liquid-button {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
  }

  .liquid-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
  }

  .liquid-button:hover::before {
    left: 100%;
  }

  /* Data Visualization Glow */
  .data-viz-glow {
    filter: drop-shadow(0 0 10px rgba(59, 130, 246, 0.5));
    transition: filter 0.3s ease;
  }

  .data-viz-glow:hover {
    filter: drop-shadow(0 0 20px rgba(139, 92, 246, 0.8));
  }

  /* Cyberpunk Grid */
  .cyberpunk-grid {
    background-image: 
      linear-gradient(rgba(59, 130, 246, 0.1) 1px, transparent 1px),
      linear-gradient(90deg, rgba(59, 130, 246, 0.1) 1px, transparent 1px);
    background-size: 20px 20px;
    animation: gridMove 20s linear infinite;
  }

  @keyframes gridMove {
    0% { background-position: 0 0; }
    100% { background-position: 20px 20px; }
  }

  /* Quantum Dots */
  .quantum-dots {
    position: relative;
  }

  .quantum-dots::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 4px;
    height: 4px;
    background: radial-gradient(circle, #3B82F6 0%, transparent 70%);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 
      20px 0 0 rgba(139, 92, 246, 0.8),
      -20px 0 0 rgba(236, 72, 153, 0.8),
      0 20px 0 rgba(245, 158, 11, 0.8),
      0 -20px 0 rgba(34, 197, 94, 0.8);
    animation: quantumSpin 4s linear infinite;
  }

  @keyframes quantumSpin {
    0% { transform: translate(-50%, -50%) rotate(0deg); }
    100% { transform: translate(-50%, -50%) rotate(360deg); }
  }

  /* Slider Enhancements */
  .slider {
    -webkit-appearance: none;
    appearance: none;
    height: 8px;
    border-radius: 4px;
    background: linear-gradient(90deg, #374151, #6B7280);
    outline: none;
    transition: all 0.3s ease;
  }

  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: linear-gradient(45deg, #3B82F6, #8B5CF6);
    cursor: pointer;
    box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
    transition: all 0.3s ease;
  }

  .slider::-webkit-slider-thumb:hover {
    transform: scale(1.2);
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.8);
  }

  .slider::-moz-range-thumb {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: linear-gradient(45deg, #3B82F6, #8B5CF6);
    cursor: pointer;
    border: none;
    box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
    transition: all 0.3s ease;
  }

  .slider::-moz-range-thumb:hover {
    transform: scale(1.2);
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.8);
  }

  /* Advanced Card Hover Effects */
  .advanced-card {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    transform-style: preserve-3d;
  }

  .advanced-card:hover {
    transform: translateY(-8px) rotateX(5deg);
    box-shadow: 
      0 20px 40px rgba(0, 0, 0, 0.3),
      0 0 40px rgba(59, 130, 246, 0.2);
  }

  /* Neon Text Effect */
  .neon-text {
    color: #fff;
    text-shadow: 
      0 0 5px #3B82F6,
      0 0 10px #3B82F6,
      0 0 15px #3B82F6,
      0 0 20px #8B5CF6,
      0 0 35px #8B5CF6,
      0 0 40px #8B5CF6;
    animation: neonFlicker 2s ease-in-out infinite alternate;
  }

  @keyframes neonFlicker {
    from {
      text-shadow: 
        0 0 5px #3B82F6,
        0 0 10px #3B82F6,
        0 0 15px #3B82F6,
        0 0 20px #8B5CF6,
        0 0 35px #8B5CF6,
        0 0 40px #8B5CF6;
    }
    to {
      text-shadow: 
        0 0 2px #3B82F6,
        0 0 5px #3B82F6,
        0 0 8px #3B82F6,
        0 0 12px #8B5CF6,
        0 0 18px #8B5CF6,
        0 0 25px #8B5CF6;
    }
  }

  /* Matrix Rain Effect */
  .matrix-rain {
    position: relative;
    overflow: hidden;
  }

  .matrix-rain::before {
    content: '';
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 200%;
    background: 
      repeating-linear-gradient(
        90deg,
        transparent,
        transparent 98px,
        rgba(0, 255, 0, 0.03) 100px
      );
    animation: matrixFall 3s linear infinite;
  }

  @keyframes matrixFall {
    0% { transform: translateY(-100%); }
    100% { transform: translateY(0); }
  }

  /* Responsive Design Enhancements */
  @media (max-width: 768px) {
    .mobile-optimized {
      padding: 1rem;
    }
    
    .mobile-text {
      font-size: 0.875rem;
    }
    
    .mobile-grid {
      grid-template-columns: 1fr;
      gap: 1rem;
    }
  }

  @media (max-width: 640px) {
    .mobile-small {
      padding: 0.75rem;
      font-size: 0.8rem;
    }
  }

  /* Dark Mode Enhancements */
  .dark-mode-enhanced {
    background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #334155 100%);
    color: #e2e8f0;
  }

  /* Performance Optimizations */
  .gpu-accelerated {
    transform: translateZ(0);
    will-change: transform;
  }

  .smooth-scroll {
    scroll-behavior: smooth;
  }

  /* Accessibility Enhancements */
  .focus-visible:focus-visible {
    outline: 2px solid #3B82F6;
    outline-offset: 2px;
  }

  .reduced-motion {
    animation: none !important;
    transition: none !important;
  }

  @media (prefers-reduced-motion: reduce) {
    * {
      animation-duration: 0.01ms !important;
      animation-iteration-count: 1 !important;
      transition-duration: 0.01ms !important;
    }
  }
`;

