const EnhancedStepIndicator: React.FC<{ currentStep: number; totalSteps: number }> = ({ 
  currentStep, 
  totalSteps 
}) => {
  return (
    <div className="flex items-center justify-center mb-8">
      <div className="flex items-center space-x-4 rtl:space-x-reverse">
        {Array.from({ length: totalSteps }, (_, i) => i + 1).map((step) => (
          <React.Fragment key={step}>
            <motion.div
              className={`relative w-12 h-12 rounded-full flex items-center justify-center text-sm font-bold transition-all duration-500 ${
                step === currentStep
                  ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white shadow-lg scale-110 pulse-glow'
                  : step < currentStep
                  ? 'bg-gradient-to-r from-green-500 to-blue-500 text-white'
                  : 'bg-gray-700 text-gray-400'
              }`}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
            >
              {step < currentStep ? (
                <motion.div
                  initial={{ scale: 0 }}
                  animate={{ scale: 1 }}
                  transition={{ delay: 0.2 }}
                >
                  <CheckCircle className="w-6 h-6" />
                </motion.div>
              ) : (
                <motion.span
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ delay: 0.1 }}
                >
                  {step}
                </motion.span>
              )}
              
              {step === currentStep && (
                <motion.div
                  className="absolute inset-0 rounded-full border-2 border-white/30"
                  initial={{ scale: 1, opacity: 1 }}
                  animate={{ scale: 1.5, opacity: 0 }}
                  transition={{ duration: 1.5, repeat: Infinity }}
                />
              )}
            </motion.div>
            
            {step < totalSteps && (
              <motion.div
                className={`w-8 h-1 transition-all duration-500 ${
                  step < currentStep 
                    ? 'bg-gradient-to-r from-green-500 to-blue-500' 
                    : 'bg-gray-700'
                }`}
                initial={{ scaleX: 0 }}
                animate={{ scaleX: step < currentStep ? 1 : 0 }}
                transition={{ delay: step < currentStep ? 0.3 : 0 }}
              />
            )}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
};

